.strobe {
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-animation: strobeStart .5s ease-out, strobe 1s infinite;
          animation: strobeStart .5s ease-out, strobe 1s infinite;
  -webkit-animation-delay: 0s, .5s;
          animation-delay: 0s, .5s;
}

@-webkit-keyframes strobeStart {
  0% {
    -webkit-transform:  skew(0deg,0deg) scaleY(1) ;
            transform:  skew(0deg,0deg) scaleY(1) ;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
   }
  40% {
    -webkit-transform:  skew(0deg,0deg) scaleY(.9);
            transform:  skew(0deg,0deg) scaleY(.9);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  100% { -webkit-transform:   skew(4deg,0deg) scaleX(1); transform:   skew(4deg,0deg) scaleX(1); }
}

@keyframes strobeStart {
  0% {
    -webkit-transform:  skew(0deg,0deg) scaleY(1) ;
            transform:  skew(0deg,0deg) scaleY(1) ;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
   }
  40% {
    -webkit-transform:  skew(0deg,0deg) scaleY(.9);
            transform:  skew(0deg,0deg) scaleY(.9);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  100% { -webkit-transform:   skew(4deg,0deg) scaleX(1); transform:   skew(4deg,0deg) scaleX(1); }
}

@-webkit-keyframes strobe {
  0% { -webkit-transform:   skew(4deg,0deg) scaleX(1); transform:   skew(4deg,0deg) scaleX(1); }
  10% { -webkit-transform:  skew(1deg,0deg) scaleY(.9) ; transform:  skew(1deg,0deg) scaleY(.9) ; }
  50% { -webkit-transform:  skew(-4deg,0deg) scaleX(1); transform:  skew(-4deg,0deg) scaleX(1); }
  60% { -webkit-transform:  skew(-1deg,0deg) scaleY(.9) ; transform:  skew(-1deg,0deg) scaleY(.9) ; }
  100% {-webkit-transform: skew(4deg,0deg) scaleX(1);transform: skew(4deg,0deg) scaleX(1); }
}

@keyframes strobe {
  0% { -webkit-transform:   skew(4deg,0deg) scaleX(1); transform:   skew(4deg,0deg) scaleX(1); }
  10% { -webkit-transform:  skew(1deg,0deg) scaleY(.9) ; transform:  skew(1deg,0deg) scaleY(.9) ; }
  50% { -webkit-transform:  skew(-4deg,0deg) scaleX(1); transform:  skew(-4deg,0deg) scaleX(1); }
  60% { -webkit-transform:  skew(-1deg,0deg) scaleY(.9) ; transform:  skew(-1deg,0deg) scaleY(.9) ; }
  100% {-webkit-transform: skew(4deg,0deg) scaleX(1);transform: skew(4deg,0deg) scaleX(1); }
}

